#root {
    display: flex;
    flex-direction: column;
    height: 100vh;
}
main {
    flex-grow: 1;
    margin-top: 100px;
}
main.catalog {
    margin-top: 0;
}
footer.catalog {
    margin-right: 400px;
}
a {
    text-decoration: none;
    color: #ffffff;
    background-color: #1976d2;
}
.count {
    margin: 0 7px;
}
.decrease, .increase {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    background: #1976d2;
    border: none;
    width: 30px;
    height: 30px;
    border-radius: 5px;
    cursor: pointer;
}
.decrease:hover, .increase:hover {
    background-color: #1565c0;
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.15), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
}